import { Col, Row } from "antd";
import 'antd/dist/antd.css';
import React from "react";
import * as styles from '../../assets/css/portfolio.module.css';
import CategoryAlbum from "./CategoryAlbum";
import AlbumCover from '../../assets/images/album.jpg';


const Portfolio = () => {

    return(
        <div className={styles.container}>
            <Row gutter={[20,20]} justify="space-between">
                <Col span={8}>
                    <CategoryAlbum title="Семья" cover={AlbumCover}/>
                </Col>
                <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <CategoryAlbum title="Дети" cover={AlbumCover}/>
                </Col>
                <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <CategoryAlbum title="Беременность" cover={AlbumCover}/>
                </Col>
                <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <CategoryAlbum title="Love Story" cover={AlbumCover}/>
                </Col>
                <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <CategoryAlbum title="Индивидуальная съёмка" cover={AlbumCover}/>
                </Col>
                <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <CategoryAlbum title="Крещение" cover={AlbumCover}/>
                </Col>
            </Row>
        </div>
    );
};

export default Portfolio;