import React from "react";
import * as styles from '../../assets/css/portfolio.module.css';



const CategoryAlbum = ({title, cover}) => {

    return(
        <div className={styles.albumContainer}>
            <img src={cover} className={styles.albumCover} alt="Album Cover"/>
            <h3 className={styles.albumTitle}>
                {title}
            </h3>
        </div>
    );
};

export default CategoryAlbum;